:root {
  --bakgroundColor: #101010;
}

.dashboardContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  margin: auto;
  flex: 0 1 auto;
  width: 100%;
  max-width: 1280px;

  background: url("../../images/gradient-sidebar.png") var(--bakgroundColor);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: auto;
}

.layoutContainer {
  /* width: 100%; */
  display: flex;
  flex-direction: row;
}

.sideBarContainer {
  /* width: auto; */
  display: flex;
  flex-direction: column;
}

@media (max-width: 1024px) and (min-width: 769px) {
  .layoutContainer {
    flex-direction: column;
  }

  .sideBarContainer {
    gap: 32px;
    justify-content: center;
    flex-direction: row;
  }
}

